@import 'main';
// End Of Import

// Mob Nav
.menuTriger {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: rgba($color-sec, 0.6);
    position: fixed;
    top: 25px;
    right: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    z-index: 99;
    display: none;

    @include Sm{
        display: flex;
    }

    i{
        color: #ffffff;
        font-size: 20px;
    }
}

.navover {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    z-index: 9;
    transition: .5s;
    opacity: 0;
    visibility: hidden;

    &.open {
        opacity: 1;
        visibility: visible;
    }
}

.sideNav {
    position: fixed;
    top: 0;
    right: -261px;
    width: 260px;
    height: 100%;
    background-color: #FFF;
    z-index: 99;
    overflow-y: auto;
    transition: .5s;

    &.open{
        right: 0;
    }

    .options {
        padding: 20px;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .lang a {
            display: flex;
            align-items: center;
            justify-content: center;
            color: #888;
        }

        .close1 {
            display: flex;
            font-size: 20px;
            color: #dedede;
            cursor: pointer;
        }
    }

    & > ul {
        display: flex;
        align-items: center;
        flex-direction: column;
        height: 100%;
        width: 100%;
        // overflow: auto;
        padding: 0px 25px 0;

        & > li{
            position: relative;
            padding: 7px 15px;
            width: 100%;

            &:not(:last-child) {
                border-bottom: 1px solid #dedede;
            }

            a {
                width: 100%;
                display: block;
                font-size: 16px;
                color: $color-sec;
                transition: .5s;
            }

            i{
                position: absolute;
                top: 13px;
                left: 13px;
                transform: rotate(90deg);
                font-size: 9px;
                color: currentColor;
                transition: .5s;

                &.open{
                    transform: rotate(0deg);
                }
            }

            .drop{
                width: 100%;
                padding: 5px 15px;
                display: none;
                overflow: visible;
                height: auto;

                li{
                    padding: 4px 0;

                    a{
                        font-size: 15px;
                        color: #898989;
                        transition: .5s;
                        
                        &:hover{
                            color: #595959;
                        }
                    }
                }
            }
        }
    }
}

// Mob Nav

// Head

.head{

    background-image: url('../images/header.jpg');
    background-size: cover;
    background-repeat: no-repeat;

    @include Md{
        background-position: 59.5% 0%;
    }

    @include Lg{
        background-position: 50% 0%;
    }

    .container{
        position: relative;
    }

    .up{
        position: absolute;
        top: 0;
        left: 0;
        width: max-content;
        @extend .flix;
        padding-top: 15px;
        padding-left: 15px;


        .lang{
            font-size: 12px;
            color: #b0b0b0;
            margin-right: 10px;
            transition: .5s;

            &:hover{
                color: $color-black;
            }
        }
    
        @include Sm{
            display: none;
        }
    }

    .content{
        padding: 10px 0;
        display: flex;
        align-items: center;
        justify-content: space-between;

        @include Sm{
            flex-direction: column;
        }
    }

    .search{
        position: relative;
        width: max-content;
        margin-top: 25px;
        flex-basis: 25%;

        &.open{
            opacity: 1;
            visibility: visible;
            top: 60%;

            .bar{
                cursor: pointer;

                span{
                    opacity: 1;
                }
            }
        }

        @include Sm{
            order: 3;
            position: absolute;
            top: 100%;
            z-index: 999;
            opacity: 0;
            visibility: hidden;
            display: flex;
            justify-content: center;
        }

        @include Xs{
            margin-top: 10px;
        }

        // @include mob480{
        //     width: 100%;
        // }

        .bar{
            display: none;
            width: 40px;
            height: 40px;
            position: relative;
            
            span{
                display: block;
                width: 30px;
                height: 2px;
                background-color: #888;
                position: absolute;
                transition: .3s;
                
                &:first-child{
                    transform-origin: left;
                    transform: rotate(47deg);
                    top: 25%;
                    left: 10px;
                }

                &:last-child{
                    transform-origin: right;
                    transform: rotate(-47deg);
                    top: 25%;
                    right: 10px;
                }
            }

            @include Sm{
                display: flex;
                align-items: center;
                justify-content: center;
                position: absolute;
                top: -85px;
            }
        }

        input{
            width: 260px;
            height: 55px;
            border: 0;
            border-bottom: 1px solid #e2e2e2;
            padding: 0 10px;
            color: #b6b6b6;
            transition: .5s;
            background-color: transparent;

            &::placeholder{
                color: #b6b6b6;
            }

            &:focus{
                border-bottom: 1px solid $color-black;
            }

            @include Sm{
                width: 600px;
                background-color: rgba($color-black, 0);
                border-bottom: 1px solid #3d3d3d;
                
                &:focus{
                    border-bottom: 1px solid #888;
                }
            }

            @include Xs{
                width: 400px;
            }

            @include mob480{
                width: 300px;
            }
        }

        button{
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: 30px;
            border-radius: 50%;
            background-color: transparent;
            border: 0;
            display: flex;
            align-items: center;
            justify-content: center;
            transition: .5s;

            @include Md{
                left: 10px;
            }

            @include Lg{
                left: 30px;
            }

            @include Sm{
                left: 10px;
            }

            // @include mob480{
            //     left: 45px;
            // }

            svg{
                    
                path{
                    transition: .5s ease;
                    fill: $color-sec;

                    @include Sm{
                        fill: #c3c3c3;
                    }
                }
            }

            &:hover{

                svg{

                    path{
                        fill: $color-prim;
                    }
                }
            }
        }
    }

    .overlay{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.9);
        z-index: 9;
        display: none;
        align-items: center;
        justify-content: space-evenly;
        flex-direction: column;
        z-index: 99;
    }

    .menu{
        margin-top: 25px;
        display: flex;
        justify-content: flex-end;
        align-items: center;
        flex-basis: 25%;

        & > div{

            &:hover{
                svg{

                    path{
                        fill: $color-prim;
                    }
                }
            }

            svg{

                path{
                    transition: .5s;
                    fill: $color-sec;
                }
            }

            &:not(:last-child){

                svg{
                    margin-left: 25px;
                }
            }

            &.user{
                position: relative;
                cursor: pointer;

                &:hover{
                    
                    .drop{
                        visibility: visible;
                        opacity: 1;
                        transform: translateY(0px);
                    }
                }

                .drop{
                    position: absolute;
                    top: 100%;
                    right: -101%;
                    width: 132px;
                    transform: translateY(10px);
                    background-color: rgb(255, 255, 255);
                    text-align: center;                    
                    box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.1);
                    opacity: 0;
                    visibility: hidden;
                    z-index: 9;
                    transition: .5s;

                    &:after{
                        content: "";
                        width: 0;
                        height: 0;
                        display: block;
                        border-width: 14px;
                        border-color: transparent transparent #ffffff;
                        border-style: solid;
                        top: -19px;
                        position: absolute;
                        right: 50%;
                        transform: translateX(50%);
                    }

                    ul{
                        padding: 5px 13px;

                        li{
                            padding: 0 12px;

                            a{
                                color: $color-sec;
                                transition: .5s;
                                font-size: 14px;
                                line-height: 2;

                                &:hover{
                                    color: $color-prim;
                                }
                            }

                            &:not(:last-child){
                                border-bottom: 1px solid #f2f2f2;
                            }

                            a{
                                transition: .5s;
                            }
                        }
                    }
                }
            }

            &.cart{
                position: relative;

                span{
                    position: absolute;
                    top: 50%;
                    right: 50%;
                    transform: translate(45%, -35%);
                    color: $color-sec;

                    @include Sm{
                        top: 47%;
                        right: 25%;
                    }
                }

                @include Md{

                    svg{
                        margin-left: 0;
                    }
                }
            }

            &.searBtn{
                display: none;

                @include Sm{
                    display: block;
                }
            }
        }
    }
}

// Head

// Navbar

.navBar{
    background-color: $color-sec;
    height: 50px;
    @extend .flix;

    @include Sm{
        display: none;
    }

    & > ul{
        display: flex;
        align-items: center;
        justify-content: center;

        & > li{
            position: relative;

            i{
                font-size: 8px;
                margin-right: 5px;
            }

            &:not(:last-child){
                margin-left: 25px;
            }

            a{
                font-size: 17px;
                line-height: 2.752;
                color: $color-white;
                transition: .5s;
                
                &:hover{
                    color: $color-prim;
                }
            }
        }
    }
}

// Navbar

.owl-nav{
    position: absolute;
    top: 49%;
    transform: translate(50%,-50%);
    right: 50%;
    width: 100%;
    height: 0;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include Sm{
        display: none;
    }

    button{

        &.owl-prev{
            span{
                border-left: 0;
                border-bottom: 0;
            }
        }

        &.owl-next{
            span{
                border-right: 0;
                border-top: 0;
            }
        }

        span{
            display: block;
            width: 15px;
            height: 15px;
            border: 1px solid $color-prim;
            transform: rotate(45deg);
        }
    }
}

// Slider Styles

.owl-dots{
    position: absolute;
    bottom: 40px;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;

    @include Xs{
        bottom: 15px;
    }
    
    button{

        &:not(:last-child){
            margin-left: 5px;
        }

        &.active{

            span{
                background-color: $color-prim;
            }
        }

        span{
            display: block;
            width: 10px;
            height: 10px;
            background-color: rgba($color-prim, 0.3);
        }
    }
}

// Main Slider

.main-slider{


    .owl-nav{
        position: absolute;
        top: 49%;
        transform: translate(50%,-50%);
        right: 50%;
        width: 100%;
        height: 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 50px;

        @include Sm{
            display: none;
        }

        button{
            width: 30px;
            height: 30px;
            display: flex;
            justify-content: center;
            align-items: center;
            transition: .5s;

            &:hover{
                span{
                    border: 1px solid $color-sec;
                }
            }

            &.owl-prev{
                span{
                    border-left: 0;
                    border-bottom: 0;
                    transform: rotate(45deg) translate(-3px, 3px);
                }
            }

            &.owl-next{
                transform: translateX(-0.4px);
                span{
                    border-right: 0;
                    border-top: 0;
                    transform: rotate(45deg) translate(3px, -3px);
                }
            }

            span{
                display: block;
                width: 15px;
                height: 15px;
                border: 1px solid #fff;
                transform: rotate(45deg);
                transition: .5s;
            }
        }
    }

    .owl-dots{
        position: absolute;
        bottom: 40px;
        right: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        display: none;

        @include Sm{
            display: flex;
        }

        @include mob600{
            bottom: 10px;
        }
        
        button{

            &:not(:last-child){
                margin-left: 5px;
            }

            &.active{

                span{
                    background-color: $color-prim;
                }
            }

            span{
                display: block;
                width: 10px;
                height: 10px;
                background-color: rgba($color-prim, 0.5);
            }
        }
    }
}

// Main Slider

.sliders{
    position: relative;
    padding-bottom: 30px;
    z-index: 1;

    &:after{
        content: "";
        width: 95.5%;
        height: 100%;
        border: 1px solid $color-sec;
        display: block;
        margin: auto;
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        z-index: 1;
    }
}

.disc.custom{
    position: relative;
    margin: 30px 0;
    // background-color: #f7f7f7;
    // height: max-content;
    z-index: 9;

    &:not(:first-child){
        margin-top: 60px;
    }

    .disc-head{
        position: relative;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 20px;
        margin-top: 37px;

        @include Xs{
            width: 94%;
            margin: 37px auto 37px;
        }
    
        & > p{
            font-size: 22px;
            font-family: "frtBold";
            color: $color-sec;
            margin-bottom: 0;
            margin-right: 25px;

            @include Sm{
                margin-right: 0;
            }

            @include mob375{
                font-size: 20px;
            }
        }
    
        & > a{
            font-family: "frtBold";
            color: #a5a5a5;
            transition: .5s;
            font-size: 14px;
            color: rgb(207, 207, 207);

            @include Md{
                margin-left: 20px;
            }

            @include Lg{
                margin-left: 0px;
            }

            &:hover{
                color: $color-prim;
            }
        }
    }
    
    .owl-carousel{

        .owl-stage{
            padding: 10px 0;
        }

        .owl-item{
            display: flex;
            justify-content: center;
        }

        .owl-nav{
            position: absolute;
            top: 40%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 105%;
            height: 0;
            display: flex;
            align-items: center;
            justify-content: space-between;
            font-size: 30px;
            margin-top: 0;
            color: #c4c4c4;
            padding: 0;
            right: unset;

            @include Md{
                width: 97%;
            }

            @include Lg{
                width: 105%;
                top: 40%;
            }

            @include Sm{
                display: none;
            }

            button{
                width: 15px;
                height: 15px;
                border: 2px solid #d0d0d0;
                border-right: none;
                border-bottom: none;
                transform: rotate(-45deg);
                border-radius: 1px;
                transition: .5s;

                &.owl-prev{
                    transform: rotate(135deg);
                }

                &:hover{
                    background-color: transparent;
                    border: 2px solid $color-prim;
                    border-right: none;
                    border-bottom: none;
                }

                span{
                    display: none;
                }
            }
        }

        .owl-dots{
            display: none;

            @include Sm{
                display: flex;
                justify-content: center;
                align-items: center;
                position: unset;
                transform: translateY(35px);
                margin-bottom: 15px;
            }
        }

        .owl-item{

            img{
                width: initial;
                margin: auto;
            }
        }
    }
}

.product{
    text-align: center;
    width: 216px;
    background-color: transparent;
    overflow: hidden;
    @extend .flix;
    flex-direction: column;
    transition: .5s;

    &:hover{
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
        // padding-bottom: 15px;

        .pic{
            border: 1px solid transparent;

            img{
                animation: flash 1.5s;
            }
        }
    }

    .pic{
        height: 240px;
        width: 100%;
        border: 1px solid #f2f2f2;
        overflow: hidden;
        position: relative;
        @extend .flix;
        transition: .5s;

        // @include Md{
        //     height: 235px;
        // }

        // @include Lg{
        //     height: 290px;
        // }

        &:hover{
            &:after{
                animation: shape .5s linear;
            }
        }

        &:after{
            content: "";
            position: absolute;
            top: -103%;
            left: -100%;
            width: 100%;
            height: 170%;
            transition: .7s ease;
            background-image: linear-gradient(to right, transparent, rgba($color-white, 0.3));
            transform: rotate(45deg);
        }

        .top{
            width: 30px;
            height: 30px;
            background-color: rgba($color-prim, 0.8);
            position: absolute;
            top: 20px;
            right: 20px;
            z-index: 9;
            color: $color-white;
            @extend .flix;
            transform: rotate(45deg);

            & > span{
                transform: rotate(-45deg);
                font-size: 12px;
                font-family: 'frtBold';
            }

            // @include Md{
            //     top: 66px;
            // }

            // @include Lg{
            //     top: 10px;
            // }
        }
    }

    .detail{
        padding: 15px 0 0px 0;
        background-color: transparent;
    }

    & > a{
        display: flex;
        align-items: center;
        justify-content: center;
    }

    // &:hover{
    //     box-shadow: 0px 0px 15px 0px rgba(0, 0, 0, 0.1);
    // }

    & > img{
        width: initial;
        height: 167px;
        display: block;
        margin: auto;
    }

    .pro-title{
        font-size: 14px;
        font-family: "frtBold";
        color: #000;
        // margin-top: 0px;
        transition: .5s;

        &:hover{
            color: $color-sec;
        }
    }

    span{
        font-size: 16px;
        font-family: "frtBold";
        display: inline-block;

        &.price{
            color: $color-sec;
            margin-right: 8px;
        }

        &.discount{
            color: #bbbbbb;
            text-decoration: line-through;
        }
    }
}

// Boxes

.boxes{
    margin-top: 80px;
    position: relative;
    z-index: 9;

    .col-md-4{
        &:not(:last-child){
            .box{
                @include Sm{
                    margin-bottom: 25px;
                }
            }
        }

        .box{
            @include Xs{
                width: 95%;
                margin: 0 auto;
            }


        }
    }

    .box{
        height: 135px;
        padding: 0 15px;
        border: 1px solid #ededed;
        @extend .flix;
        flex-direction: column;
        // justify-content: space-between;
        text-align: center;

        &:hover{
            img{
                animation: shake .3s linear;
            }
        }

        .pic{
            @extend .flix;
            height: 50px;
            margin-bottom: 10px;
        }

        img{
            transition: .5s;
        }

        p{
            font-size: 16px;
            color: #000000;
        }

        @include Sm{
            padding: 0 25px;
        }
    }
}

// Boxes

// footer

footer{
    padding: 70px 0 0;
    padding-bottom: 0;
    background-image: url("../images/footer.png");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 50% 0;
    margin-top: 30px;

    @include Sm{
        padding: 40px 0 0;
    }

    .foot-links{

        @include Lg{
            text-align: right;
        }

        @include Sm{
            margin-bottom: 15px;
        }

        .collapse{
            display: block;

            @include Sm{
                display: none;
            }

            &.in{
                display: block;
            }
        }

        & > button{
            font-size: 16px;
            font-family: "frtBold";
            color: #fff;
            margin-bottom: 25px;
            background-color: transparent;
            border: 0;
            padding: 0;
            pointer-events: none;

            &.trans{

                &:after{
                    content: "\2212";
                }
            }

            @include Sm{
                width: 100%;
                margin: auto;
                pointer-events: all;
                margin-bottom: 0;
                display: flex;
                align-items: center;
                justify-content: space-between;
            }

            @include mob480{
                width: 100%;
            }

            &:after{
                content: "\002B";
                margin-left: 5px;
                font-size: 18px;
                float: left;
                display: none;

                @include Sm{
                    display: block;
                }
            }
        }

        ul{

            @include Sm{
                padding-top: 10px;
                padding-right: 10px;
            }

            li{
                &:not(:last-child){
                    margin-bottom: 10px;
                }
            }
        }

        a{
            font-size: 14px;
            font-family: "frt";
            color: #7f7f7f;
            transition: .5s;

            &:hover{
                color: $color-prim;
            }
        }
    }

    .newsletter{
        
        @include Md{
            margin-top: 35px;
            text-align: center;
        }

        @include Lg{
            margin-top: 0;
            text-align: right;
        }

        @include Sm{
            margin-top: 35px;
            text-align: center;
        }

        & > p{
            font-size: 16px;
            font-family: "frtBold";
            color: #fff;
            margin-bottom: 20px;
        }

        & > span{
            font-size: 14px;
            font-family: "frt";
            color: #fff;
            margin-bottom: 15px;
            display: block;
        }
    }

    .frm{
        position: relative;
        margin-bottom: 17px;
        width: 360px;

        @include Md{
            margin: auto;
            margin-bottom: 17px;
        }

        @include Sm{
            margin: auto;
            margin-bottom: 17px;
        }

        @include mob375{
            width: 280px;
        }

        input{
            padding: 0 20px;
            height: 40px;
            width: 100%;
            border: 0;
            border-bottom: 1px solid #4c7c7c;
            color: #4f4f4f;
            font-size: 14px;
            background-color: transparent;
            transition: .5s;

            &::placeholder{
                color: #4f4f4f;
            }

            &:focus{
                border-bottom: 1px solid #fff;
            }
        }

        button{
            position: absolute;
            top: 50%;
            left: 10px;
            transform: translateY(-50%);
            width: 37px;
            height: 30px;
            border: 0;
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: transparent;

            &:hover{

                svg{

                    path{
                        fill: $color-prim;
                    }
                }
            }

            svg{
                
                path{
                    transition: .5s;
                }
            }
        }
    }
}

